.mint-swipe {
  overflow: hidden;
  position: relative;
  /* height: 0px; */
  margin-top: 30px;
}
.mint-swipe-items-wrap {
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}
.mint-swipe-items-wrap > div {
  position: absolute;
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
  width: 100%;
  height: 100%;
  display: none;
}
.mint-swipe-items-wrap > div.is-active {
  display: block;
  -webkit-transform: none;
  transform: none;
}
.mint-swipe-indicators {
  position: absolute;
  bottom: 10px;
  width: 100%;
  text-align: center;
}
.mint-swipe-indicator {
  width: 23px;
  height: 23px;
  text-align: center;
  display: inline-block;
  color: white;
  /* background: #000; */
  /* opacity: .5; */
  margin: 0;
  border-radius: 50%;
  padding: 1px;
  line-height: 23px;
  font-size: 16px;
}
.is-active {
  /* border:solid 1px */
}
@media (max-width: 767px) {
.mint-swipe-indicator {
    width: 16px;
    height: 16px;
    line-height: 16px;
    font-size: 13px;
}
.mint-swipe {
    margin-top: 10px;
}
}
@media (min-width: 1636px) {
.mint-swipe-indicator {
    width: calc(100vh/845*23);
    height: calc(100vh/845*23);
    line-height: calc(100vh/845*23);
    font-size: calc(100vh/845*16);
    margin-bottom: calc(100vh/845*10);
}
.mint-swipe {
    margin-top: 10px;
}
}

















































